
.yy-home-wraper {
  overflow: auto;
  width: 100%;
  height: 100%;
}
.yy-home-header {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0.13333333rem;
  background: #ffffff;
  box-sizing: border-box;
}
.yy-home-search {
  width: 76%;
  position: relative;
}
.iconfont.icon-fangdajing {
  position: absolute;
  left: 0.05333333rem;
  top: 0.09333333rem;
  font-size: 0.24rem;
  color: #aeaeae;
}
.yy-home-search-input {
  width: 100%;
  height: 0.32rem;
  /* border-color: #dfdfdf; */
  /* border-radius: 5px; */
  text-indent: 0.32rem;
  outline: none;
}
.yy-home-go-address {
  width: 18%;
  height: 0.4rem;
  line-height: 0.4rem;
  background: orange;
  text-align: center;
  border-radius: 5%;
  margin-left: 0.2rem;
}
.yy-home-service {
  width: 100%;
  height: 2.4rem;
}
.yy-home-service-item {
  float: left;
  width: 15%;
}
.yy-home-service-img {
  width: 100%;
}
.yy-home-service-img img {
  width: 100%;
}


